@import 'mixins';
.woocommerce{

/* CART */

    & table.tamilano-cart{
        margin-top: 30px;
        border: none;
        border-bottom:1px solid $orange;
        th{
            font-weight: $regular;
            color: $blue;
            text-align: center;
            font-size: 18px;
        }
        td{
            border-color: $orange;
            color: $orange;
            text-align: center;
            font-size: 20px;
            padding: 20px 0;
            &.product-name{
                text-align: left;
                padding-left:50px;
                & .product-image{
                    display: inline-block;
                    & img{
                        width: 100px;
                    }
                }
                & .product-title{
                    display: inline-block;
                    & a{    
                        margin-left: 10px;
                        color: $orange;
                        text-transform: uppercase;
                        font-size: 18px;
                        font-weight: $bold;
                        &.remove{
                            font-size: 13px;
                            color: $light-grey !important;
                            text-transform: none;
                            font-weight: $regular;
                            &:hover{background: none;
                            }
                        }
                    }  
                }
            }
            & .quantity{
                & input{ 
                    border: 1px solid $orange;
                    border-radius: 0;
                    color: $orange;
                    &::-webkit-inner-spin-button {
                        opacity: 1;
                        margin: 0;
                        background: $white !important;
                        color: $orange !important;
                    }
                }
            }
        }
    }

    & table.tamilano-total{
        border: none;
        color:$blue;
        text-align: right;
        & th{
            border:none !important;
        }
        & td{
            border:none;
        }

        & tr.cart-subtotal{
            font-size: 20px;
            font-weight: $bold;        
        }
    }

    & .cart-button{
        & .orange-button{
            width:100%;
            background: $white;
            &:hover{
                background: $orange;
            }
            &.checkout-button{
                background: $orange;
                color: $white;
                &:hover{
                    background: $white;
                    color: $orange;
                }
            }
        }
    }

/* MESSAGES */

& .woocommerce-notices-wrapper{
    margin: 15px 0;
    & .woocommerce-message{
        background: $white;
        color:$blue;
        border: none;
        border-bottom: 1px solid $orange;
        &::before{
            color: $orange;
        }
        & a{
            color: $orange;
        }
    }
}


/* SINGLE PRODUCT */

& .product{
    padding: 10px 0 40px 0;
    & .woocommerce-product-gallery{
        &.tamilano-product-image-wrap{
            width: 100%;
            pointer-events: none;
            & .woocommerce-product-gallery__trigger{
                display: none;
            }
            & .tamilano-product-image{
                text-align: right;     
            }
        }
    }

    & .tamilano-product-summary{
        margin-top: 5px;
      & .product-title{
          color: $orange;
          text-transform: uppercase;
          font-size: 20px;
          font-weight: $bold;
      }
      & .price{
        color: $blue;
        font-size: 20px;
        font-weight: $bold;
        margin-top: 15px;
        border-bottom: 1px solid $orange;
        padding-bottom: 15px;
      }
      & .product-description{
        margin-top: 15px;
        color: $blue;
        font-size: 16px;
        }
      & .add-to-cart-wrap{
          border-top: 1px solid $orange;
          padding: 15px 0;
          & .quantity{
              & input{
                border: 1px solid $orange;
                border-radius: 0;
                color: $orange;
                padding: 5px 0 6px 0;
            }
          }
      }
      & .text-premio{
        font-size: 12px;
        color: $blue;
      }

    }
    & .woocommerce-tabs.tamilano-tabs{
        border-top: 1px solid $orange;
        margin-top: 30px;
        & ul.tabs{
            text-align: center;
            border: none;
            margin: 0;
            &:before,&:after{border: none;}
            & li{
                padding:10px 30px;
                background: none;
                border: none;
                border-radius: 0;
                &:before,&:after{border: none;}
                & a{
                    text-transform: uppercase;
                    color: $blue;
                }
                &.active{
                    & a{
                        color: $orange;
                    }
                }
            }
        }
        & .woocommerce-Tabs-panel{
            padding: 40px 100px;
            line-height: 30px;
            color: $blue;
            text-align: left;
            border-bottom: 1px solid $orange;
        }
    }


}
& .products{ 
    & .product{
        margin-top: 20px;
        & .product-image{
            & .tinv-wraper.tinv-wishlist{
                position: absolute;
                top:10px;
                right:10px;
                color: $orange;
                font-size: 20px;
            }
            & .prize-patch{
                position: absolute;
                top:10px;
                left:10px;
                color: $orange;
            }
        }
        & .product-title{
            & h2{
                font-size: 16px;
                font-weight: $bold;
                color: $blue;
                margin-top: 10px;
                text-transform: uppercase;
            }
        }
        & .price{
            font-weight: $bold;
            color: $orange;
            margin-top: 10px;
        }
        & .add_to_cart_button{
            float: right;
            text-align: center;
            color: $orange;
            border: 1px solid $orange;
            text-transform: uppercase;
            font-size: 14px;
            font-weight: $bold;
            padding: 7px 25px;
            border-radius: 0;
            background: $white;
        }
    }
} 
}

